
.header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background-color: #ea6f5a;
}
.header .tit_l {
    -webkit-box-flex: 8;
        -ms-flex: 8;
            flex: 8;
}
.tit_l span {

    font-size: 16px;
    font-family: "微软雅黑";
    color: #fff;
    text-align: center;
    display: block;
    height: 60px;
    line-height: 60px;
}
.header .tit_r {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.tit-r-box {
    position: relative;
}
.tit-r-box a {
    position: absolute;
    right: 25%;
    margin-top: 50%;
    font-size: 20px;
    font-family: cursive;
    color: #fff;
}







